@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
*{margin:0;padding:0;font-family:"Manrope", sans-serif}.App h1{font-size:64px;margin:20px 0px 0px 20px}.App ul{margin:0px 20px 20px 20px}.App ul li{margin-top:30px;list-style:none}.App ul li .main-card{display:flex;position:relative;cursor:pointer;margin:0px 20px 20px 20px;box-shadow:0px 0px 13px 2px rgba(0,0,0,0.1);padding:15px}.App ul li .main-card h1{margin:0px 0px 0px 10px;text-align:center;width:50px;height:53px;font-size:36px;color:white;background:linear-gradient(180deg, #00a9a7 0%, #00ba89 100%)}.App ul li .main-card p{font-size:18px;font-weight:bold;line-height:50px;padding:0px 0px 0px 30px}.App ul li .main-card .border{transition:0.4s;border-bottom:2px solid #00adaa;position:absolute;bottom:0px;left:0px;width:200px}.App ul li .side-card{display:flex;overflow:hidden;margin:0px 20px 15px 20px;transition:1s ease}.App ul li .side-card img{padding:0px 0px 0px 40px;cursor:pointer}.App ul li .side-card p{font-weight:normal;padding:0px 0px 0px 20px;font-size:18px}@media (max-width: 480px){.App h1{font-size:42px}}

